import React,{ Component } from "react";


export default class Skills extends Component{
    render() {
        return (
            <div className='skills mt-4'>
                <div className="header">
                    <div className="row">
                        <div className="col-4">
                            <hr />
                            
                        </div>
                        <div className="col-4 text-uppercase">
                            <p>Skill's</p>
                        </div>
                        <div className="col-4">
                            <hr/>
                        </div>
                    </div>
                </div>
                
                <div className="content text-left">
                <div className="row">
                    <div className="col-4 font-weight-bold text-secondary">
                        <h5> Language</h5>
                    </div>
                    <div className="col-8">
                        <p>C, C++, HTML, CSS, Bootstrap,JavaScript</p>
                    </div>
                </div>

                <div className="row">
                    <div className="col-4 font-weight-bold text-secondary">
                        <h5> Database</h5>
                    </div>
                    <div className="col-8">
                        <p>MySQL, MongoDB</p>
                    </div>
                </div>

                <div className="row">
                    <div className="col-4 font-weight-bold text-secondary">
                        <h5> Web Technology</h5>
                    </div>
                    <div className="col-8">
                        <p>HTML, CSS, React.js, Bootstrap4, MaterialUI</p>
                    </div>
                </div>

                <div className="row">
                    <div className="col-4 font-weight-bold text-secondary">
                        <h5> Tools</h5>
                    </div>
                    <div className="col-8">
                        <p>VS Code, IntelliJ , sublime</p>
                    </div>
                </div>
                </div>
                
            </div>
            
        )
}
}