import React, { Component } from "react";
import atanu from '../img/my-pic.jpg'
import { FaGithub,FaTwitter,FaLinkedin } from 'react-icons/fa';

export default class LeftImage extends Component{
    render() {
        return (
            <div className="img-container">
                <div className="img-card">
                    <div className="card border-info ml-2 mb-3 d-flex justify-content-center">
                        <img className='card-img-top' src={atanu} alt="" />
                        <div className="card-body">
                            <h6 className='card-text'>Atanu Mondal</h6>
                            <p className="card-subtitle">Student,Web Developer</p>
                        </div>
                        <div className="card-footer">
                            <div className="social-liks">
                            <a href="https://github.com/atanu12" target='_blank' className='text-secondary'><FaGithub  /></a>
                            <a href="https://twitter.com/bindas_bapi_" target='_blank'  className='text-secondary pl-2'><FaTwitter  /></a>
                            <a href="https://www.linkedin.com/in/atanu-mondal30/" target='_blank'  className='text-secondary pl-2'><FaLinkedin  /></a><br/><br/>
                            </div>
                        </div>
                        
                    </div>
                </div>
            </div>  
        )
    }
}